let Ajax = require("../util/Ajax.js")
export default {
	data() {
		return {
			isLogin:true
		}
	},
	methods:{
		goLogin:function(){
			this.isLogin=true;
		},
		goRegister:function(){
			this.isLogin=false;
		},
		sendLogin:function(e){
			var data={
				user:this.username,
				pass:this.password
			};
			var url ="";
			var that = this;
			if(data.user=="" || data.user==undefined){
				uni.showToast({
					title:'用户名不能为空',
					icon:"none"
				})
				return 0;
			}else if(data['user'].length<2 || data['user'].length>=30){
				uni.showTabBar({
					title:"用户名长度再2-30之间",
					icon:'none'
				})
				return 0;
			}
			Ajax.get({url:'user/login',data:data})
			  .then((res)=>{//请求成功的回调函数
				if(res.data.code==1024){
					uni.showToast({
						title:"登录成功"
					})
					uni.setStorageSync("session",res.data.session);
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},1000)
				}else{
					uni.showToast({
						title:"登录失败",
						icon:"none"
					})
				}
				uni.hideLoading();
			  }).catch((res)=>{//捕获请求失败时的回调函数
			    console.log(res)
			  })
		},
		sendRegister:function(){
			
		}
	},
	onLoad(e) {
		this.isLogin=e.isLogin;
		
	}
	}
